	module.exports = {"data":{"markdownRemark":{"html":"<p>現存する私の作品の中では、最古の 3DCG。\nかつ PC を使って作成した、最古の CG 作品でもある。\nもっと前にもいくつか作った記憶があるが、完全に消失している。</p>\n<p>当時作っていたオリジナルコミック “<a href=\"/cats_story\">CATS STORY</a>” の挿絵として作った3D絵。\n11 歳位の頃に描いていたオリジナルコミック “あぶないネコでんせつ” の世界観を使ったシリアス物ストーリー。\nシリアス物ストーリーを作ったのはたぶんこれが初。\nまぁ、結構稚拙なストーリーなので公開は気が向いたときにでも。</p>\n<p>そういえば、大学入りたての時にこのストーリーを今一度 RPG 化しようとして頓挫した覚えがある。</p>\n<p>モデルは DOGA-L2 を、エフェクトや文字は HyperPaint を使用。</p>","frontmatter":{"cover":null,"date":"1997-12-01T00:00:00+09:00","strDate":"1997/11/30","title":"[3D] 地球"}}},"pathContext":{"path":"/ja/blog/1997/12/01-3d-earth/","langKey":"ja"}}

